<template>
    <div v-if="visible" class="fixed inset-0 bg-black bg-opacity-40 z-50 flex items-center justify-center">
      <div class="bg-white w-full max-w-md rounded-lg shadow-lg p-6 relative">
        <h2 class="text-xl font-bold mb-4">💸 Salary Payment - {{ staff?.name }}</h2>
  
        <div class="mb-4">
          <label class="block text-sm font-medium mb-1">Amount</label>
          <input v-model.number="payment.amount" type="number" class="w-full border px-3 py-2 rounded" />
        </div>
  
        <div class="mb-4">
          <label class="block text-sm font-medium mb-1">Date</label>
          <input v-model="payment.date" type="date" class="w-full border px-3 py-2 rounded" />
        </div>
  
        <div class="mb-4">
          <label class="block text-sm font-medium mb-1">Type</label>
          <select v-model="payment.type" class="w-full border px-3 py-2 rounded">
            <option value="salary">Salary</option>
            <option value="advance">Advance</option>
          </select>
        </div>
  
        <div class="mb-4">
          <label class="block text-sm font-medium mb-1">Note</label>
          <input v-model="payment.note" type="text" class="w-full border px-3 py-2 rounded" />
        </div>
  
        <div class="flex justify-end space-x-2">
          <button @click="close" class="px-4 py-2 rounded bg-gray-300 hover:bg-gray-400">Cancel</button>
          <button @click="addPayment" class="px-4 py-2 rounded bg-blue-600 text-white hover:bg-blue-700">Add Payment</button>
        </div>
  
        <div class="mt-6">
          <h3 class="text-lg font-semibold mb-2">📜 Last 5 Payments</h3>
          <table class="min-w-full text-sm border border-gray-200 rounded-lg">
            <thead class="bg-gray-100 text-left text-gray-600 uppercase text-xs">
              <tr>
                <th class="px-3 py-2">#</th>
                <th class="px-3 py-2">Amount</th>
                <th class="px-3 py-2">Date</th>
                <th class="px-3 py-2">Type</th>
                <th class="px-3 py-2">Note</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(p, i) in payments"
                :key="i"
                class="border-t hover:bg-gray-50 transition-all"
              >
                <td class="px-3 py-2">{{ i + 1 }}</td>
                <td class="px-3 py-2 font-semibold text-green-600">₹{{ p.amount }}</td>
                <td class="px-3 py-2">{{ new Date(p.date).toLocaleDateString() }}</td>
                <td class="px-3 py-2 capitalize">{{ p.type }}</td>
                <td class="px-3 py-2 text-gray-500 italic">{{ p.note || '-' }}</td>
              </tr>
              <tr v-if="!payments.length">
                <td colspan="5" class="text-center text-gray-400 px-4 py-4">No payments found.</td>
              </tr>
            </tbody>
          </table>
        </div>
  
        <button @click="close" class="absolute top-3 right-4 text-gray-600 hover:text-red-500 text-lg">×</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'StaffPaymentModal',
    props: {
      visible: Boolean,
      staff: Object
    },
    data() {
      return {
        payment: {
          amount: '',
          date: '',
          type: 'salary',
          note: ''
        },
        payments: [{ amount: 15000, date: '2025-03-01', type: 'salary', note: 'March' },
          { amount: 5000, date: '2025-02-20', type: 'advance', note: 'Advance' },
          { amount: 15000, date: '2025-02-01', type: 'salary', note: 'Feb' },
          { amount: 15000, date: '2025-01-01', type: 'salary', note: 'Jan' },
          { amount: 5000, date: '2024-12-15', type: 'advance', note: 'Emergency' }
          ]
      };
    },
    watch: {
      visible(val) {
        if (val && this.staff?._id) {
          this.fetchPayments();
        }
      }
    },
    methods: {
      close() {
        this.$emit('close');
        this.payment = { amount: '', date: '', type: 'salary', note: '' };
        this.payments = [];
      },
      async fetchPayments() {
        // Replace with your API call
        this.payments = [];
      },
      async addPayment() {
        if (!this.payment.amount || !this.payment.date) return alert('Enter amount and date');
        this.payments.unshift({ ...this.payment });
        this.payment = { amount: '', date: '', type: 'salary', note: '' };
      }
    }
  };
  </script>
  
  <style scoped>
  input::-webkit-inner-spin-button,
  input::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  </style>
  