<template>
    <div v-if="visible" class="fixed inset-0 z-50 bg-black bg-opacity-50 flex items-center justify-center">
        <div class="bg-white w-full max-w-md rounded-lg p-6 shadow-xl relative">
            <h2 class="text-xl font-bold mb-4">💳 Payments for {{ tenant?.name }}</h2>

            <div class="mb-4">
                <label class="block text-sm font-medium mb-1">Amount</label>
                <input v-model.number="payment.amount" type="number" class="w-full border px-3 py-2 rounded" />
            </div>

            <div class="mb-4">
                <label class="block text-sm font-medium mb-1">Date</label>
                <input v-model="payment.date" type="date" class="w-full border px-3 py-2 rounded" />
            </div>

            <div class="mb-4">
                <label class="block text-sm font-medium mb-1">Note (optional)</label>
                <input v-model="payment.note" type="text" class="w-full border px-3 py-2 rounded" />
            </div>

            <div class="flex justify-end gap-2">
                <button @click="close" class="px-4 py-2 rounded bg-gray-300 hover:bg-gray-400">Cancel</button>
                <button @click="addPayment" class="px-4 py-2 rounded bg-emerald-600 text-white hover:bg-emerald-700">Add
                    Payment</button>
            </div>

            <div class="mt-6">
                <h3 class="text-lg font-semibold mb-2">📜 Last 5 Payments</h3>
                <div class="overflow-x-auto">
                  <table class="min-w-full text-sm border border-gray-200 rounded-lg">
                    <thead class="bg-gray-100 text-left text-gray-600 uppercase text-xs">
                      <tr>
                        <th class="px-3 py-2">#</th>
                        <th class="px-3 py-2">Amount</th>
                        <th class="px-3 py-2">Date</th>
                        <th class="px-3 py-2">Note</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(p, i) in payments"
                        :key="i"
                        class="border-t hover:bg-gray-50 transition-all"
                      >
                        <td class="px-3 py-2">{{ i + 1 }}</td>
                        <td class="px-3 py-2 font-semibold text-emerald-600">₹{{ p.amount }}</td>
                        <td class="px-3 py-2">{{ new Date(p.date).toLocaleDateString() }}</td>
                        <td class="px-3 py-2 text-gray-500 italic">{{ p.note || '-' }}</td>
                      </tr>
                      <tr v-if="!payments.length">
                        <td colspan="4" class="text-center text-gray-400 px-4 py-4">No payments found.</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
              

            <button @click="close" class="absolute top-3 right-4 text-gray-600 hover:text-red-500 text-lg">×</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'PaymentModal',
    props: {
        visible: Boolean,
        tenant: Object
    },
    data() {
        return {
            payment: {
                amount: '',
                date: '',
                note: ''
            },
            payments: [{ amount: 5000, date: '2025-03-01', note: 'March rent' },
            { amount: 5000, date: '2025-02-01', note: 'February rent' },
            { amount: 5000, date: '2025-01-01', note: 'January rent' },
            { amount: 5000, date: '2024-12-01', note: 'December rent' },
            { amount: 5000, date: '2024-11-01', note: 'November rent' }
            ]
        };
    },
    watch: {
        visible(val) {
            if (val && this.tenant?._id) {
                // this.fetchPayments();
            }
        }
    },
    methods: {
        close() {
            this.$emit('close');
            this.payment = { amount: '', date: '', note: '' };
            this.payments = [];
        },
        async fetchPayments() {
            const res = await fetch(`/payments?tenantId=${this.tenant._id}`, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                }
            });
            this.payments = await res.json();
        },
        async addPayment() {
            if (!this.payment.amount || !this.payment.date) return alert('Enter amount and date');
            await fetch('/payments', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                },
                body: JSON.stringify({ ...this.payment, tenantId: this.tenant._id })
            });
            this.payment = { amount: '', date: '', note: '' };
            this.fetchPayments();
        }
    }
};
</script>

<style scoped>
input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
</style>